.btn {
  padding: 16px 40px;
  transition: background-color 0.5s;
  border: 0;
  border-radius: 6px;
  background: #e64980;
  color: #fff;
  font-size: 1.6rem;
  font-weight: bold;
  cursor: pointer; }
  .btn:hover {
    background: #fe6a94; }
