@import "../../../scss/main"; // it's  also ✔️

.btn {
  height: 48px;
  padding: 16px 40px;

  transition: all 0.5s;
  border: 0;
  border-radius: 6px;
  // background: #e64980;
  background: $color-brand;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    background: $color-success;
  }

}

.link--ib {
  // composes: btn; // compose === local @extend
  display: inline-block;
  transition: all 0.5s;

  &:hover {
    background-color: #000;
  }
}
